<template>
    <element-base
        :class="isActive ? 'active' : ''"
        :is-first="isFirst"
        :is-last="isLast"
        :errors="errors"
    >
        <template slot="header">
            <h2 class="h3">{{ element.label }}</h2>
        </template>
        <template slot="content">
            <div class="form-group">
                <input
                    v-model="value"
                    type="text"
                    :placeholder="element.params.placeholder"
                    class="form-control form-control-lg"
                />
                <small
                    v-if="element.params.description"
                    class="form-text text-muted"
                >
                    {{ element.params.description }}
                </small>
            </div>
        </template>
    </element-base>
</template>

<script>
import ElementBase from './ElementBase';

export default {
    components: {
        'element-base': ElementBase,
    },
    props: ['isFirst', 'isLast', 'isActive', 'element'],
    data() {
        return {
            value: '',
            errors: [],
            isValidated: false,
            isValid: false,
            validations: [
                {
                    condition: 'this.value === ""',
                    message: 'Make sure you answer the question.',
                },
            ],
        };
    },
    methods: {
        formValidate() {
            this.errors = [];

            for (let key in this.validations) {
                const v = this.validations[key];

                if (eval(v.condition)) {
                    this.errors.push(v.message);
                    this.isValid = false;
                }
            }

            this.isValidated = true;

            if (!this.errors.length) {
                this.isValid = true;

                return true;
            }
        },
    },
};
</script>
