<template>
    <element-base
        :class="isActive ? 'active' : ''"
        :is-first="isFirst"
        :is-last="isLast"
        :errors="errors"
    >
        <template slot="header">
            <h2 class="h3">{{ element.label }}</h2>
        </template>
        <template slot="content">
            <div class="form-group">
                <div class="row">
                    <div class="form-group col-md-6">
                        <label>First name</label>
                        <input
                            v-model="input.firstName.value"
                            type="text"
                            class="form-control form-control-lg"
                            :class="
                                !input.firstName.isValid && isValidated
                                    ? 'is-invalid'
                                    : ''
                            "
                            @keyup="formValidate()"
                        />
                    </div>
                    <div class="form-group col-md-6">
                        <label>Last name</label>
                        <input
                            v-model="input.lastName.value"
                            type="text"
                            class="form-control form-control-lg"
                            :class="
                                !input.lastName.isValid && isValidated
                                    ? 'is-invalid'
                                    : ''
                            "
                            @keyup="formValidate()"
                        />
                    </div>
                    <div class="form-group col-md-12">
                        <label>E-mail</label>
                        <input
                            v-model="input.email.value"
                            type="email"
                            class="form-control form-control-lg"
                            :class="
                                !input.email.isValid && isValidated
                                    ? 'is-invalid'
                                    : ''
                            "
                            @keyup="formValidate()"
                        />
                    </div>
                </div>
            </div>
        </template>
    </element-base>
</template>

<script>
import ElementBase from './ElementBase';

export default {
    components: {
        'element-base': ElementBase,
    },
    props: ['isFirst', 'isLast', 'isActive', 'element'],
    data() {
        return {
            input: {
                firstName: {
                    value: '',
                    isValid: false,
                },
                lastName: {
                    value: '',
                    isValid: false,
                },
                email: {
                    value: '',
                    isValid: false,
                },
            },
            errors: [],
            isValidated: false,
            validations: [
                {
                    input: 'firstName',
                    condition: 'this.input.firstName.value === ""',
                    message: 'Make sure you provide your first name.',
                },
                {
                    input: 'lastName',
                    condition: 'this.input.lastName.value === ""',
                    message: 'Make sure you provide your first name.',
                },
                {
                    input: 'email',
                    condition: 'this.input.email.value === ""',
                    message: 'Make sure you provide a valid e-mail address.',
                },
            ],
        };
    },
    methods: {
        formValidate() {
            this.errors = [];

            for (let key in this.validations) {
                const v = this.validations[key];

                if (eval(v.condition)) {
                    this.errors.push(v.message);
                    this.input[v.input].isValid = false;
                } else {
                    this.input[v.input].isValid = true;
                }
            }

            this.isValidated = true;

            if (!this.errors.length) {
                return true;
            }
        },
    },
};
</script>
