<template>
    <element-base
        :class="isActive ? 'active' : ''"
        :is-first="isFirst"
        :is-last="isLast"
        :errors="errors"
    >
        <template slot="header">
            <h2 class="h3">{{ element.label }}</h2>
        </template>
        <template slot="content">
            <div
                v-for="(option, key) of element.params.options"
                class="form-group"
            >
                <div class="custom-control custom-radio">
                    <input
                        :id="`input-${element.id}-${key}`"
                        v-model="value"
                        type="radio"
                        :value="key"
                        name="customRadio"
                        :class="!isValid && isValidated ? 'is-invalid' : ''"
                        class="custom-control-input"
                        @change="formValidate()"
                    />
                    <label
                        class="custom-control-label btn btn-lg btn-light"
                        :for="`input-${element.id}-${key}`"
                    >
                        {{ option.label }} ({{ option.points }} points)
                    </label>
                </div>
            </div>
        </template>
    </element-base>
</template>

<script>
import ElementBase from './ElementBase';

export default {
    components: {
        'element-base': ElementBase,
    },
    props: ['isFirst', 'isLast', 'isActive', 'element'],
    data() {
        return {
            value: '',
            errors: [],
            isValidated: false,
            isValid: false,
            validations: [
                {
                    condition: 'this.value === ""',
                    message: 'Make sure you answer the question.',
                },
            ],
        };
    },
    methods: {
        formValidate() {
            this.errors = [];

            for (let key in this.validations) {
                const v = this.validations[key];

                if (eval(v.condition)) {
                    this.errors.push(v.message);
                    this.isValid = false;
                }
            }

            this.isValidated = true;

            if (!this.errors.length) {
                this.isValid = true;

                return true;
            }
        },
    },
};
</script>
