<template>
    <div class="carousel-item">
        <div class="row">
            <div class="col-md-6 offset-md-3">
                <div
                    class="card card-light"
                    :class="!isFormValid ? 'shake' : ''"
                >
                    <div class="card-body">
                        <slot name="header"></slot>
                        <hr />
                        <slot name="content"></slot>
                    </div>
                    <div class="card-footer d-flex justify-content-between">
                        <button
                            v-if="!isFirst"
                            class="btn btn-warning"
                            @click="$parent.$parent.prevItem()"
                        >
                            <i
                                class="fas fa-angle-left mr-2"
                                aria-hidden="true"
                            ></i>
                            <span class="d-none d-sm-inline-block">
                                <strong>Previous</strong>
                            </span>
                        </button>

                        <button
                            v-if="!isFirst && !isLast"
                            class="btn btn-warning"
                            @click="formSubmit()"
                        >
                            <span class="d-none d-sm-inline-block">
                                <strong>Next</strong>
                            </span>
                            <i
                                class="fas fa-angle-right ml-2"
                                aria-hidden="true"
                            ></i>
                        </button>

                        <button
                            v-if="isFirst"
                            class="btn btn-warning"
                            @click="formSubmit()"
                        >
                            <span class="d-none d-sm-inline-block">
                                <strong>Start</strong>
                            </span>
                            <i
                                class="fas fa-angle-right ml-2"
                                aria-hidden="true"
                            ></i>
                        </button>

                        <button
                            v-if="isLast"
                            class="btn btn-warning"
                            @click="formSubmit()"
                        >
                            <span class="d-none d-sm-inline-block">
                                <strong>View Report</strong>
                            </span>
                            <i
                                class="fas fa-angle-right ml-2"
                                aria-hidden="true"
                            ></i>
                        </button>
                    </div>
                </div>
                <div class="w-100">
                    <div
                        v-for="error of errors"
                        v-if="errors.length"
                        class="alert alert-danger mt-3"
                    >
                        <strong>An error occured!</strong> {{ error }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['errors', 'isFirst', 'isLast'],
    data() {
        return {
            isFormValid: true,
        };
    },
    methods: {
        formSubmit() {
            this.isFormValid = this.$parent.formValidate();

            if (this.isFormValid) {
                this.$parent.$parent.nextItem();
            }
        },
    },
};
</script>
