<template>
    <element-base
        :class="isActive ? 'active' : ''"
        :is-first="isFirst"
        :is-last="isLast"
        :errors="errors"
    >
        <template slot="header">
            <h2 class="h3">{{ element.label }}</h2>
        </template>
        <template slot="content">
            <div class="form-group">
                <input
                    v-model="number"
                    type="number"
                    :min="element.params.min"
                    :max="element.params.max"
                    :class="!isValid && isValidated ? 'is-invalid' : ''"
                    class="form-control form-control-lg"
                    @keyup="formValidate()"
                />
            </div>
        </template>
    </element-base>
</template>

<script>
import ElementBase from './ElementBase';

export default {
    components: {
        'element-base': ElementBase,
    },
    props: ['isFirst', 'isLast', 'isActive', 'element'],
    data() {
        return {
            number: '',
            errors: [],
            isValidated: false,
            isValid: false,
            validations: [
                {
                    condition: 'this.number === ""',
                    message: 'Make sure you answer the question.',
                },
                {
                    condition:
                        'parseInt(this.number) > this.element.params.max',
                    message:
                        'The value you provided exceeds the maximum amount.',
                },
                {
                    condition:
                        'parseInt(this.number) < this.element.params.min',
                    message: 'The value you provided is too low.',
                },
            ],
        };
    },
    methods: {
        formValidate() {
            this.errors = [];

            for (let key in this.validations) {
                const v = this.validations[key];

                if (eval(v.condition)) {
                    this.errors.push(v.message);
                    this.isValid = false;
                }
            }

            this.isValidated = true;

            if (!this.errors.length) {
                this.isValid = true;

                return true;
            }
        },
    },
};
</script>
