const KEY = 'roboform-state';

export default class StateService {
    constructor() {
        const storage = localStorage.getItem(KEY);

        if (storage !== null) {
            const rows = JSON.parse(storage);

            for (let i in rows) {
                this[i] = rows[i];
            }
        }
        else {
            this.init();
        }
    }

    init() {
        this.elements = [];
        this.save();
    }

    set(key, value) {
        this[key] = value;
        this.save();

        console.info(`Set "${value}" for "${key}" in localStorage.`);
    }

    remove(key) {
        delete this[key];
        this.save();
    }

    save() {
        const val = JSON.stringify(this);
        localStorage.setItem(KEY, val);
    }
}
