<template>
    <div
        id="carouselExampleIndicators"
        class="h-100 carousel slide"
        data-ride="carousel"
    >
        <ol class="carousel-indicators">
            <li
                v-for="el of slides"
                data-target="#carouselExampleIndicators"
                :class="current == el.id ? 'active' : ''"
            ></li>
        </ol>

        <div class="carousel-inner h-100 d-flex align-items-center">
            <div :class="" class="w-100">
                <template v-for="el of slides">
                    <component
                        :is="el.type"
                        :is-active="current == el.id"
                        :is-first="el.isFirst"
                        :is-last="el.isLast"
                        :element="el"
                    />
                </template>
                <report v-if="isFinished" />
            </div>
        </div>
    </div>
</template>

<script>
import FormService from '../services/FormService.ts';
import ElementService from '../services/ElementService.ts';
import StateService from '../services/StateService.ts';

import ElementIntro from './ElementIntro';
import ElementText from './ElementText';
import ElementInputNumber from './ElementInputNumber';
import ElementInputRadios from './ElementInputRadios';
import ElementInputText from './ElementInputText';
import ElementInputContactDetails from './ElementInputContactDetails';

import Report from './Report';

export default {
    components: {
        elementIntro: ElementIntro,
        elementText: ElementText,
        elementInputNumber: ElementInputNumber,
        elementInputRadios: ElementInputRadios,
        elementInputText: ElementInputText,
        elementInputContactDetails: ElementInputContactDetails,
        report: Report,
    },
    data() {
        return {
            e: new ElementService(),
            f: new FormService(),
            s: new StateService(),
            isFinished: false,
            current: 0,
            slides: [],
        };
    },
    created() {
        if (this.s.elements.length) {
            for (let key in this.s.elements) {
                let id = this.s.elements[key];
                let el = this.e.requestForId(id);

                this.slides.push(el);
            }
        } else {
            let el = this.e.requestForId(21);

            this.slides.push(el);
            this.s.elements.push(21);
            this.s.save();
        }

        this.slides[0].isFirst = true;

        this.current = this.slides[this.slides.length - 1].id;

        console.log(`Current element is ${this.current}.`);
    },
    methods: {
        updateAndSave() {
            this.current = this.slides[this.slides.length - 1].id;
            this.s.save();
        },
        prevItem() {
            if (this.slides.length > 1) {
                this.slides.pop();
                this.s.elements.pop();
                this.updateAndSave();
            }
        },
        nextItem() {
            const nextElement = this.e.mockNext(this.current);

            if (nextElement.id !== this.current) {
                this.slides.push(nextElement);
                this.s.elements.push(nextElement.id);
                this.updateAndSave();
            } else {
                this.isFinished = true;
                console.log(
                    'End of form, submitting results and requesting report.'
                );
            }
        },
    },
};
</script>
